/*
# Front-end Style Guide

## Layout

The designs were created to the following widths:

- Mobile: 375px
- Desktop: 1440px

## Colors

### Primary

- Soft orange: hsl(35, 77%, 62%)
- Soft red: hsl(5, 85%, 63%)

### Neutral

Off-white: hsl(36, 100%, 99%)
Grayish blue: hsl(233, 8%, 79%)
Dark grayish blue: hsl(236, 13%, 42%)
Very dark blue: hsl(240, 100%, 5%)

## Typography

### Body Copy

- Font size (paragraph): 15px

### Font

- Family: [Inter](https://fonts.google.com/specimen/Inter)
- Weights: 400, 700, 800
*/

@font-face {
  font-family: Regular;
  src: url("./Imported/assets/fonts/static/Inter-Regular.ttf");
}
@font-face {
  font-family: Bolded;
  src: url("./Imported/assets/fonts/static/Inter-Bold.ttf");
}
@font-face {
  font-family: ExtraBold;
  src: url("./Imported/assets/fonts/static/Inter-ExtraBold.ttf");
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Regular;
}

.body {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.main-card {
  width: 80%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-top: 2rem;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 10%;
}

.header-logo {
  font-weight: 800;
}

.menu-bar {
  display: none;
}

.navbar {
  display: flex;
  flex-direction: row;
  list-style: none;
  height: 100%;
  justify-content: center;
  align-items: center;
}

.navbar-link {
  margin-right: 2.75rem;
  cursor: pointer;
}

.grid-container {
  padding: 1rem;
  display: grid;
  height: 90%;
  width: 100%;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(5, 1fr);
  gap: 1rem;
}

.cover-photo {
  grid-column: 1 / 3;
  grid-row: 1 / 3;
  background-image: url("./Imported/assets/images/image-web-3-desktop.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.new-headline-container {
  grid-column: 3;
  grid-row: 1 / 4;
  background-color: hsl(240, 100%, 5%);
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: hidden;
}

.new {
  color: hsl(35, 77%, 62%);
  font-size: 3rem;
  font-weight: 800;
  height: 20%;
  width: 90%;
  margin: 0 auto;
  margin-top: 1rem;
}

.new-card {
  margin-bottom: 0.75rem;
  width: 90%;
  cursor: pointer;
  transition: all 0.3s ease-in;
}

.new-card:hover {
  transform: scale(1.01);
}

.new-card:hover .new-card-line {
  height: 3px;
}

.new-card h3 {
  color: hsl(36, 100%, 99%);
  font-weight: 700;
  margin-bottom: 0.8rem;
}

.new-card p {
  color: hsl(233, 8%, 79%);
}

.new-card-line {
  margin-top: 0.3rem;
  height: 1px;
  background-color: hsl(233, 8%, 79%);
  width: 100%;
  margin: 1.25rem auto;
  transition: all 0.3s ease;
}

.main-headline {
  grid-column: 1;
  grid-row: 3;
  word-wrap: break-word;
}

.main-headline h1 {
  font-size: 3rem;
  font-family: Bolded;
  color: hsl(240, 100%, 5%);
}

.sub-heading {
  grid-column: 2;
  grid-row: 3;
  /* background-color: aquamarine; */
  position: relative;
}

.read-button {
  padding: 1rem 2.5rem;
  background-color: hsl(5, 85%, 63%);
  float: left;
  letter-spacing: 0.3rem;
  color: whITE;
  cursor: pointer;
  position: absolute;
  bottom: 0;
  transition: all 0.3s ease;
}

.read-button:hover {
  border: 1px solid hsl(5, 85%, 63%);
  background-color: white;
  color: hsl(5, 85%, 63%);
}

.card {
  display: flex;
  margin-top: 1rem;
  cursor: pointer;
  transition: all 0.2s ease;
}

.card:hover {
  transform: scale(1.025);
}

.card-image {
  height: 8rem;
}

.card-content {
  width: 75%;
  display: flex;
  margin-left: 1rem;
  flex-direction: column;
  word-wrap: break-word;
}

.card-content h2 {
  font-size: 2.15rem;
  font-weight: Bolded;
  color: hsl(233, 8%, 79%);
}

.card-content h4 {
  font-size: 1.25rem;
  margin-bottom: 0.25rem;
  font-weight: Bolded;
  color: hsl(240, 100%, 5%);
}

.card-content p {
  font-size: 1rem;
}

.one {
  grid-column: 1;
  grid-row: 4;
}
.two {
  grid-column: 2;
  grid-row: 4;
}
.three {
  grid-column: 3;
  grid-row: 4;
}

@media screen and (max-width: 1280px) and (min-width: 910px) {
  .new-card p {
    display: none;
  }

  .new-headline-container {
    justify-content: space-around;
  }

  .new-card h3 {
    font-size: 1.5rem;
  }
}

@media screen and (max-width: 910px) {
  .body {
    margin-bottom: 3rem;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .main-card {
    width: 90%;
    display: flex;

    align-items: center;
    justify-content: center;
  }

  .grid-container {
    display: block;
    flex-direction: column;
  }

  .navbar {
    display: none;
  }

  .menu-bar {
    display: inline;
    cursor: pointer;
  }

  .cover-photo {
    height: 20rem;
    margin-bottom: 1rem;
  }

  .main-headline h1 {
    font-size: 3.5rem;
  }

  .sub-heading {
    height: 10rem;
    margin: 1rem auto;
    margin-bottom: 2rem;
    font-size: 1.1rem;
  }

  .read-button {
    width: 18rem;
    height: 4rem;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .new-headline-container {
    justify-content: space-around;
    height: 45rem;
    margin-bottom: 2rem;
  }

  .new {
    font-size: 3.5rem;
    display: flex;
    align-items: center;
    margin-top: -1rem;
  }

  .new-card {
    margin-bottom: 2rem;
  }

  .new-card h3 {
    font-size: 1.5rem;
  }

  .new-card p {
    font-size: 1.25rem;
  }
}

.menu {
  position: fixed;
  top: 0;
  right: 0;
  background-color: white;
  height: 100vh;
  width: 65vw;
  z-index: 995;
  display: flex;
  flex-direction: column;
  transition: all 0.2s ease;
}

.menu-close {
  display: flex;
  justify-content: end;
  margin-top: 2.65rem;
  margin-right: 2rem;
}

.menu-navbar {
  list-style: none;
  display: flex;
  flex-direction: column;
  margin-top: 8rem;
  margin-left: 2rem;
}

.navbar-link-menu {
  padding: 1.25rem;
  font-size: 1.5rem;
  font-weight: 700;
  cursor: pointer;
}

.body-dark {
  background-color: rgba(93, 95, 121, 0.3);
  z-index: 900;
  position: fixed;
  height: 100%;
  width: 100%;
}

.hidden {
  display: none;
}
